  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
@font-face {
  font-family: "Arial";
  src: url(/static/media/Arial.07d53b5b.ttf);
}
.inner-auto-layout {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  position: relative;
  background-color: white;
}
.inner-auto-layout .hightlight {
  background-color: #F6C02A;
  color: white;
  padding: 1px 2px 1px 2px;
}
.inner-auto-layout .test {
  position: absolute;
  top: 5px;
  right: 0px;
  z-index: 99;
}
.inner-auto-layout .chart-card .wrapper {
  width: 100%;
  height: 95%;
}
.inner-auto-layout .chart-card {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
  background: #FFFFFF;
  box-shadow: 0px 0px 10px 0 rgba(179, 203, 214, 0.5);
  padding-bottom: 15px;
  position: relative;
}
.inner-auto-layout .chart-card .delete:hover {
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/delete_yellow.png") no-repeat center;
  background-size: 10px 10px;
  background-color: white;
}
.inner-auto-layout .chart-card .delete {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/delete.png") no-repeat center;
  background-size: 10px 10px;
  background-color: white;
  border-radius: 10px;
  cursor: pointer;
}
.inner-auto-layout .chart-card .editButton {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 20px;
  height: 20px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/edit_black.png") no-repeat center;
  background-size: 13px 13px;
  background-color: white;
  box-shadow: 0 1px 2px 0 rgba(155, 155, 155, 0.5);
  border-radius: 1px;
  display: none;
  cursor: pointer;
}
.inner-auto-layout .chart-card .editButton:hover {
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/edit_yellow.png") no-repeat center;
  background-size: 13px 13px;
  background-color: white;
}
.inner-auto-layout .chart-card .caption {
  width: 100%;
  background-color: white;
  font-size: 20px;
  line-height: 18px;
  font-family: "Arial", "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  color: #525252;
  font-weight: 400;
  text-align: center;
  margin-top: 7px;
  padding: 0px 23px;
}
.inner-auto-layout .chart-card .vis-box {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: white;
}
.inner-auto-layout .chart-card .vis-box .bound-box {
  width: 100%;
  height: 100%;
  overflow: scroll;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
